import Vue from 'vue'
import Router from 'vue-router'

const an = (resolve) => {
  import ('~/components/an.vue').then((module) => {
    resolve(module)
  })
}

const navs = (resolve) => {
  import ('~/components/navs.vue').then((module) => {
    resolve(module)
  })
}

const home = (resolve) => {
  import ('~/components/home.vue').then((module) => {
    resolve(module)
  })
}

const people = (resolve) => {
  import ('~/components/people.vue').then((module) => {
    resolve(module)
  })
}

const yaoClass = (resolve) => {
  import ('~/components/yaoClass.vue').then((module) => {
    resolve(module)
  })
}

const productList = (resolve) => {
  import ('~/components/productList.vue').then((module) => {
    resolve(module)
  })
}

const bell = (resolve) => {
  import('~/components/bell.vue').then((module) => {
    resolve(module)
  })
}


Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: '',
    component: home
  }, {
    path: '/navs',
    name: '',
    component: navs
  }, {
    path: '/people/:id',
    name: 'people',
    component: people
  }, {
    path: '/yaoClass',
    name: '',
    component: yaoClass
  }, {
    path: '/productList/:id',
    name: 'productList',
    component: productList
  }, {
    path: '/bell',
    name: '',
    component: bell
  }]
})


