<style lang="scss" scoped>
img{
    height: auto;
    width:100%;
}
@media (max-width: 575px) {
  .homeEnd {
      height: 130px;
      color: #fff;
      margin-top: 50px;
      background-color: #461B93;
      .homeEnd-box {
          margin: 0 30px;
      }
      .homeEnd-left {
          padding-top: 40px;
          display: inline-block;
          p {
              font-size: 15px;
              margin: 0;
          }
          a {
              display: inline-block;
              margin: 5px 5px 0 0;
