          float: right;
          padding-top: 15px;
        }
        .homeEnd-right {
            float: right;
            margin-right: 15px;
            transition: 0.3s;
            display: inline-block;
            line-height: 32px;
            text-align: center;
            color: #8d99ab;
            background-color: transparent;
            width: 32px;
            height: 32px;
            font-size: 32px;
            vertical-align: middle;
            i {
                line-height: 130px;
            }
        }
    }
}
</style>



