<template>
  <div id="app">
    <header>
      <ul>
        <li>
          <router-link to="/yaoClass">
            <i class="el-icon-goods"></i>
          </router-link>
        </li>
        <li>
          <router-link to="/bell">
            <i class="el-icon-star-off"></i>
          </router-link>
        </li>
        <li>
          <router-link to="/">
            <img src="http://ooi407n8x.bkt.clouddn.com/an.jpg" alt="">
          </router-link>
        </li>
        <li>
          <router-link to="/people/0">
            <i class="el-icon-service"></i>
          </router-link>
        </li>
        <li>
          <router-link to="/navs">
            <i class="el-icon-tickets"></i>
          </router-link>
        </li>
      </ul>
    </header>
    <main>
      <transition name="fade" mode="out-in">
        <router-view></router-view>
      </transition>
    </main>
    <!-- <homeEnd></homeEnd> -->
  </div>
</template>

<script>

import homeEnd from "~/components/homeEnd.vue";

export default {
  data() {
    return {};
  },
  components: {
    homeEnd
  },
  methods: {
    menu() {
      window.scrollTo(0,0);
    }
  },
  created(){
    this.menu();
  }
};
</script>

<style lang="scss">
#app {
  font-family: Helvetica, sans-serif;
}

.fade-enter-active,
.fade-leave-active {
  transition: all 0.1s ease;
}

.fade-enter,
.fade-leave-active {
  opacity: 0;
}

header {
  padding: 5px 10px;
  background: #461b93;
  width: 100%;
  display: inline-flex;
  justify-content: space-between;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 50;
  ul {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    text-align: center;
    li {
      width: 20%;
    }
  }
  img {
    width: 2.2rem;
    height: 2.2rem;
    border-radius: 50%;
  }
  a {
    line-height: 2.2rem;
    color: #fff;
    font-size: 20px;
  }
}

main {
  padding-bottom: 70px;
}

.swiper-container {
  width: 100%;
}

.swiper-slide img {
  height: 100%;
  width: 100%;
}

.swiper-slide {
  box-sizing: border-box;
}
</style>



